/* 
NEWS STYLES
*/
.news-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  max-width: 25vh;
  max-height: 20vh;
  overflow-y: scroll;
  padding-bottom: 15px;
}

.news-container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

.news-container a {
  text-decoration: none;
  color: chartreuse;
  font-style: italic;
  font-weight: 100;
  font-size: small;
  padding: 0;
  margin: 2px 0;
}

/* 
BUY/HOLD STYLES
*/
.buyHold-container {
  display: flex;
  flex-flow: column;
  max-width: 25vw;
}
.buyHold-container .box {
  display: flex;
  flex-direction: row;
}
.buyHold-container .box p {
  width: 50%;
  font-size: large;
  text-align: right;
}
.buyHold-container .box span {
  text-align: left;
  width: 50%;
  font-size: xx-large;
  padding-right: 15px;
}
.buyHold-container .buy {
  color: chartreuse;
}
.buyHold-container .neutral {
  color: grey;
}
.buyHold-container .sell {
  color: firebrick;
}
