/*
DAILY.JS STYLES
*/
.box {
  padding: 10px 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: baseline;
          align-items: baseline;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-align: left;
}
.current {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.current p {
  width: 100%;
  text-align: center;
  font-size: 3rem;
}
.current .row p {
  padding-right: 15px;
  width: 50%;
  text-align: right;
  font-size: 1rem;
}
.current .row span {
  width: 50%;
  text-align: left;
  font-size: 3rem;
}
.historical {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.historical p {
  width: 50%;
  text-align: right;
  padding-right: 15px;
  font-family: "RocknRoll One";
}
.historical span {
  width: 50%;
  font-size: 2rem;
  text-align: left;
}
.hi-lo p {
  width: 50%;
  text-align: right;
  padding-right: 15px;
  font-family: "RocknRoll One";
}
.hi-lo span {
  width: 50%;
  font-size: 2rem;
  text-align: left;
}
