/* 
NEWS STYLES
*/
.news-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 25vh;
  max-height: 20vh;
  overflow-y: scroll;
  padding-bottom: 15px;
}

.news-container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

.news-container a {
  text-decoration: none;
  color: chartreuse;
  font-style: italic;
  font-weight: 100;
  font-size: small;
  padding: 0;
  margin: 2px 0;
}

/* 
BUY/HOLD STYLES
*/
.buyHold-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  max-width: 25vw;
}
.buyHold-container .box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.buyHold-container .box p {
  width: 50%;
  font-size: large;
  text-align: right;
}
.buyHold-container .box span {
  text-align: left;
  width: 50%;
  font-size: xx-large;
  padding-right: 15px;
}
.buyHold-container .buy {
  color: chartreuse;
}
.buyHold-container .neutral {
  color: grey;
}
.buyHold-container .sell {
  color: firebrick;
}

.symbol-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 25px 0;
}

.symbol-form .label {
  font-feature-settings: "smcp";
  font-variant: small-caps;
  font-size: x-large;
}

.symbol-form input {
  background: black;
  color: chartreuse;
  margin: 0 15px;
  height: 2.5rem;
  font-size: 2rem;
  width: 10rem;
  text-transform: uppercase;
  border: 1px solid chartreuse;
}

.symbol-form input:focus {
  background: chartreuse;
  color: black;
  scale: 1.1;
  border: none;
}

.symbol-form button {
  margin: auto 0;
  height: 2.5rem;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  font-size: x-large;
  background: black;
  color: chartreuse;
  border: 0.5px solid chartreuse;
}

.symbol-form button:hover {
  font-feature-settings: ;
  font-variant: unset;
  text-transform: uppercase;
  font-size: 1rem;
  background: chartreuse;
  color: black;
  border: 0.5px solid chartreuse;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.symbol-form button:active {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

